<template>
	<div class="authority_wrap" data-commont="新建素材">
		<el-row style="background: #fff;padding: 15px 0;border-bottom: 1px solid #eee;">
			<el-col :span="2" :offset = "21">
				<el-button icon = "el-icon-plus" type = "primary" @click="addRoleTree">添加角色</el-button>
			</el-col>
		</el-row>
		<!--修改角色弹出框-->
	    <el-dialog
		    title="修改角色"
		    :visible.sync="editDialogVisible"
		    width="40%"
		    :before-close="handleClose">
		 	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		 		<el-form-item label="角色名称:" prop="ROLE_NAME">
			    	<el-input v-model="ruleForm.ROLE_NAME" placeholder="角色名称" style="width:90%;"></el-input>
			    </el-form-item>
			    <el-form-item label="功能权限:">
			    	<el-tree
						:data="cascaderData"
						show-checkbox
						node-key="MENU_ID"
						:default-expanded-keys="rangeCheckedData"
					 	:default-checked-keys="rangeCheckedData"
						style="height: 150px;overflow:auto;"
						:props="defaultProps"
						ref="tree2">
					</el-tree>
				</el-form-item>
			</el-form>
		    <span slot="footer" class="dialog-footer">
		    	 <el-button type="primary" @click="editTreeFun('ruleForm')">修改保存</el-button>
			   
		  	</span>
		</el-dialog>
   	
		<el-table :data="roleData" 
			style="width: 100%"
			:show-header=false  
			@expand-change = "roleFun"
			:row-key="getRowKeys"
            :expand-row-keys="expands">
		    <el-table-column type="expand">
		      	<template slot-scope="scope">
		        	<div  style="height: 150px; overflow: auto;">
		        		<el-table :data="roleByUserData"style="width: 100%" v-loading = "loading"c-text="拼命加载中">
		        			<el-table-column prop="USERNAME" label="登录账号">	
					        </el-table-column>
					        <el-table-column prop="UNAME" label="用户姓名">
					        </el-table-column>
					        <el-table-column  prop="PHONE"label="手机号码" >
					        </el-table-column>
					        <el-table-column  prop="EMAIL"label="邮箱" >
					        </el-table-column>
					        <el-table-column  prop="UPASSWORD"label="岗位" >
					        </el-table-column>
					        <el-table-column label="密码重置" width="260" >
					      		<template slot-scope="scope">
					      			<el-button>密码重置</el-button>
					      		</template>
					        </el-table-column>
					    </el-table>
		        	</div>
		
	      		</template>
		    </el-table-column>
			
		    <el-table-column>
		    	<template slot-scope="scope">
		    		<span>{{scope.row.ROLE_NAME}}</span>
		    	</template>
		    </el-table-column>	
		     <el-table-column>
		    	<template slot-scope="scope">
		    		<span style="float: right;" >
		    			<el-button @click="boundMemberDiadig = true">绑定成员</el-button>
		    			<el-button type="info" icon="el-icon-edit" size="mini"  @click="editFun({id:scope.row.ROLE_ID})"></el-button>
			     		<el-button type="danger" icon="el-icon-delete" size="mini" @click ="delRoleFun({id:scope.row.ROLE_ID})"></el-button>
		    		</span>
		    	</template>
		    </el-table-column>		   
		</el-table>
		<!--删除弹出框-->
		<el-dialog
			    :visible.sync="oneDeldialogVisible"
			    width="30%"
			    :before-close="handleClose"
			    >
			    <div style="text-align: center;">
			    	<img src="../../assets/del-image/del_image.png"/>
			    	<h3	>删除操作</h3>
			    	<span>删除后不可恢复，确认要删除吗？</span>
			    </div>
			    <span slot="footer" class="dialog-footer dialog-footer_span">
				    <el-button @click="oneDeldialogVisible = false">我在想想</el-button>
				    <el-button type="primary" @click ="deleteRole">确定</el-button>
			    </span>
			</el-dialog>

		<!--绑定成员弹出框-->
		<el-dialog
		    title="选择绑定成员"
		    :visible.sync="boundMemberDiadig"
		    width="60%"
		    :before-close="handleClose">
			<el-row :gutter="20">
			 	<el-col :span="8">
			 		<h3>选择:</h3>
			 		<el-input placeholder="搜索部门"  v-model="filterText">
					</el-input>
					<el-tree
					  class="filter-tree"
					  :data="cascaderData"
					  :filter-node-method="filterNode"
					  ref="tree">
					</el-tree>
			 	</el-col>
			 	<el-col :span="14">
			 		<h3>已选</h3>
			 		<el-table
					    ref="multipleTable"
					    :data="peopleData"
					    style="width: 100%">
					    <el-table-column type="selection" width="55">
					    </el-table-column>
					    <el-table-column  label="岗位">
					        <template slot-scope="scope">{{ scope.row.post}}</template>
					    </el-table-column>
					    <el-table-column  prop="name" label="姓名">
					    </el-table-column>
					</el-table>
			 	</el-col>
			</el-row>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="boundMemberDiadig = false">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import peopleData from './peopleData' //绑定人
	import {
	  	getUserInfo,
	  	getUsers
	} from 'utils/auth';
	import {
		queryRolePort,   //角色列表
		deleteRolePort,   //删除角色
		updateRolePort,	 //更新角色
		queryRoMeCaByRoIdPort,   //编辑初始化
	}from 'api/authority'
	import {
		getUsersPort	//角色下user
	}from'api/systemUser.js'
	import {
		leftMenuPort
	} from 'api/model/leftMenu';   //权限树
		export default {
	    name: 'authority',
	    
	    data() {
	      	return {
	      		activeNames:'0',
	      		editId:'',
		        editDialogVisible: false,  //创建角色弹框
		        oneDeldialogVisible:false,   //删除弹框
		        delId:'',   //删除行id
		        roleId:'',   //角色id
		        rangeCheckedData:[],//选中tree
				cascaderData:[],
				roleData:[],
				peopleData:peopleData,
				roleByUserData:[],   //角色下用户
				boundMemberDiadig:false,  //绑定成员弹出框
				filterText:'', //绑定选择input值
				multipleSelection: [], //多选表格
				loading:false,
				//下拉表格
				getRowKeys(row) {
		            return row.ROLE_ID;
		        },
		         // 要展开的行，数值的元素是row的key值
        		expands: [],
				//tree字段命名
				defaultProps: {
		          children: 'subMenu',
		          label: 'MENU_NAME'
		        },
				//创建者弹出框
				ruleForm:{
					ROLE_NAME:''
				},
				rules: {
				 	ROLE_NAME: [
			            { required: true, message: '请输入角色名称', trigger: 'blur' },
			            { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
			        ],
				}
	      	}
	    },
	    watch: {
	        filterText(val) {
	        	this.$refs.tree.filter(val);
	        }
	    },
	    computed: {
	    	
	    },
	    created(){
	    	this.initRoleList()
	    	
	    },
	    methods:{
	    	//角色列表
	    	initRoleList(){
	    		queryRolePort().then( res =>{
	    			this.roleData = res.LISTROLEINFO
//	    			console.log('角色列表'+ JSON.stringify(res))
	    		})
	    	},
	    	//角色下user列表
	    	roleByUser(){
	    		let resData = {
	    			limit:1,    //当前页
	  				offset:5 ,//条数
	    			ROLE_ID:this.roleId
	    		}
	    		this.loading = true;
	    		getUsersPort(resData).then( res=>{
	    			this.roleByUserData = res.ALLUSER;
//	    			console.log('user列表:::'+JSON.stringify(res))
	    			this.loading = false
	    		}).catch(() =>{
		    		this.loading = false
		    	})
	    	},
//	    	//下拉
	    	roleFun(row, expandedRows){
	    		if(row){
	    			console.log('row:::'+JSON.stringify(row))
		    		console.log('expandedRows::'+JSON.stringify(expandedRows))
	     			this.roleId = row.ROLE_ID;
	     			this.expands = [];
	            	this.expands.push(row.ROLE_ID);
	     			this.roleByUser()
	     			this.editInit()
	    		}else{
	    			expandedRows = []
	    		}
     		},
	    	//编辑按钮
	    	editFun(obj){
	    		this.editId = obj.id
//	    		this.rangeCheckedData = []
	    		this.editDialogVisible = true
	    		this.editInit()
	    	//权限树	
	    		let req = {
		    		USER_ID:getUsers(),  // 
		    		RIGHTS:getUserInfo().RIGHTS,
		    		PLATFORM:'1'    //系统平台
	    	    }
		    	leftMenuPort(req).then( data => {
		    	 	this.cascaderData = data
		    	 	this.editInit()
	//		  		console.log('菜单::'+JSON.stringify(data))
			    })
		  		
	    		
	    	},
	    	  //编辑初始化	
	    	editInit(){
	    		let resData = {
	    			ROLE_ID:this.editId,
	    		}
	    		queryRoMeCaByRoIdPort(resData).then( res =>{
	    			this.rangeCheckedData = JSON.parse(res.LISTMENUINFO)
	    			this.ruleForm.ROLE_NAME = res.ROLE_NAME
	    			console.log('编辑角色'+JSON.stringify(res))
	    		})
	    	},
	    	//编辑保存
	    	editTreeFun(formName){
	    		this.$refs[formName].validate((valid) => {
					if (valid) {
			    		this.editDialogVisible = false
		    			this.rangeCheckedData = this.$refs.tree2.getCheckedKeys()
			    		let resData = {
			    			ROLE_ID:this.editId,
			    			ROLE_NAME:this.ruleForm.ROLE_NAME,
			    			PARENT_ID:'',
			    			RIGHTS:this.rangeCheckedData.map(item =>item).join(),   //权限
			    		}
			    		updateRolePort(resData).then( res =>{
			    			this.initRoleList()
			    			console.log('更新保存::::'+JSON.stringify(res))
			    		})
			    	}  else {
						console.log('error submit!!');
						return false;
					}
				});
	    	},
	    	//删除按钮
	    	delRoleFun(obj){
	    		this.delId=obj.id
	    		this.oneDeldialogVisible = true
	    	},
	    	//删除角色
	    	deleteRole(){
	    		this.oneDeldialogVisible = false
	    		deleteRolePort({ROLE_ID:this.roleId}).then( res =>{
	    			console.log('删除角色'+ JSON.stringify(res))
	    			this.initRoleList()
	    		})
	    	},

	    	//添加权限树
	    	addRoleTree(){
	    		this.$router.push({
	    			name:'creatRole_list'
	    		})
	    	},
	    	
	    	//绑定选择人员tree筛选
	    	filterNode(value, data) {
        		if (!value) return true;
        		return data.label.indexOf(value) !== -1;
      		},
	    	handleClose(done) {
		        this.$confirm('确认关闭？')
		          .then(() => {
		            done();
		          })
		          .catch(() => {});
		      	}
			}
  	}

</script>

<style lang="scss" slot-scope="scope">
	$border:1px solid #eee;
	.authority_wrap{
		
	
	}
	
</style>


